.main {
    position: relative;
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: auto 90px auto;

    grid-template-areas:
        'head head head head head . .'
        '. . . . . sort sort'
        'filter filter goods goods goods goods goods';

    padding-left: 11vw;
    padding-right: 11vw;
    padding-top: 80px;

    overflow: hidden;
}

.head {
    grid-area: head;

    display: flex;
    flex-direction: column;

    width: 100%;
    height: 341px;

    margin-top: 86px;
    margin-bottom: 230px;
    padding: 20px 15px 30px 15px;
}

.header {
    position: relative;
}

.header h2 {
    position: relative;

    top: -15px;
    left: 273px;
}

.head p,
.excuse {
    align-self: center;

    font-family: 'Oswald';
    font-size: 12px;
    letter-spacing: 0.48px;
    text-transform: uppercase;

    color: var(--customWhite);
}

.excuse {
    font-size: 16px;
}

.sort-menu {
    grid-area: sort;
}

.left-filter {
    grid-area: filter;
}

.goods {
    grid-area: goods;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 30px;

    margin-bottom: 200px;
}

.bouquet {
    margin-bottom: 30px;
}

