.tabs {
    grid-area: tabs;

    position: fixed;
    display: flex;
    justify-content: space-between;
    align-content: center;
    flex-wrap: wrap;
    gap: 50px;

    height: 50px;

    top: 100px;

    z-index: 3;
}

.exit-btn {
    position: absolute;

    right: -439px;
}

.btn,
.btn:focus {
    background-color: #0f2222;
}
