.logo {
    font-family: 'Cormorant';

    font-size: 60px;
    text-transform: uppercase;
}

.logo:hover {
    transform: scale(1.1);
}

.logo-L {
    display: block;
    position: relative;

    top: 27px;

    color: #8a8686;
}

.logo-F {
    display: block;
    position: relative;

    top: -32px;
    left: 8px;

    font-size: 50px;
    letter-spacing: 1px;

    color: var(--customWhite);
}
